
<template>
  <Form ref='formValidate' :model="formValidate" :rules="ruleValidate" label-position="left" :label-width="70">
    <Row :gutter="15">
        <Col span="6">
      <FormItem label="" prop="level">
        <Input  v-model="formValidate.level"  placeholder=""></Input>
      </FormItem>
      </Col>
        <Col span="6">
      <FormItem label="" prop="levelName">
         <Select multiple filterable v-model="formValidate.cities" placeholder="">
            <Option  v-for="item in cityList"   :value="item.cityId" :key="item.id">{{ item.cnName }}</Option>
        </Select>
      </FormItem>
      </Col>
      <Col span="6">
      <FormItem label="" prop="levelName">
        <Input  v-model="formValidate.levelName"  placeholder=""></Input>
      </FormItem>
      </Col>

      <Col span="6">
        <FormItem  prop="isEnable">
          <!-- style="margin-left:15px;margin-top:5px;" -->
          <Checkbox  v-model="formValidate.isEnable"></Checkbox>
        </FormItem>
      </Col>
    </Row>
    <FormItem>
      <Button type="primary" @click="handleSubmit()"></Button>
    </FormItem>
  </Form>
</template>

<script>
import Axios from 'axios'
import Qs from 'qs'
export default {
  data () {
    return {
      formValidate: {
        level: '',
        levelName: '',
        cities: [],
        levelType: '2',
        isEnable: true
      },
      ruleValidate: {
        level: [{
          required: true,
          // type: "number",
          message: '',
          trigger: 'blur'
        }]
        // levelName: [{
        //   required: true,
        //   message: "",
        //   trigger: "blur"
        // }]
      },
      travelList: [
        {
          id: 1,
          name: ''
        },
        {
          id: 2,
          name: ''
        }
      ],
      cityList: [],
      curMember: {}
    }
  },
  computed: {
  },
  methods: {
    cancelModal () {
      let cur_modal = this.$store.state.modal
      cur_modal.curtime = new Date().getTime()
      cur_modal.status = false
      cur_modal.component = 'Input'
      this.$store.commit('modalStatus', cur_modal)
    },
    refreshTable () {
      this.$store.commit('refreshTable', { curtime: new Date().getTime() })
    },
    getCityList () {
      Axios({
        method: 'GET',
        url: '/api/chinaCity',
        headers: {
          'userId': this.$store.state.user.userId
        }
      }).then(res => {
        this.cityList = res.data.data
      })
    },
    async handleSubmit () {
      this.$refs.formValidate.validate((valid) => {
        if (valid) {
          // this.$refs.formValidate.resetFields()
          this.submiteData(this.formValidate)
        }
      })
    },
    beforeSubmit (formData) {
      let factoryData = Object.assign(formData)
      let citiStr = factoryData.cities.join(',')
      factoryData.cities = citiStr
      factoryData.companyId = this.$route.query.id
      return factoryData
    },
    submiteData (formValidate) {
      let formData = this.beforeSubmit(formValidate)
      let sumitUrl = '/api/companyTravelLevel/add'
      //  if(this.status == 'update'){
      //    sumitUrl = '/api/companyApprover/update'
      //  }
      this.$store.commit('setCurUrl', `/api/companyTravelLevel/list?companyId=${this.$route.query.id}&city=true`)
      var data = Qs.stringify(formData)
      Axios({
        method: 'POST',
        url: sumitUrl,
        data,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
          'userId': this.$store.state.user.userId
        }
      }).then(res => {
        this.cancelModal()
        if (res.data.code === 200) {
          this.refreshTable()
        }
      })
    }
  },
  mounted () {
    this.getCityList()
    // 
  }
}
</script>

