<template>
  <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
    <Row>
      <Col span="8">
      <FormItem label="" prop="realName">
        <Input v-model="formValidate.realName" placeholder=""></Input>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="memberType">
        <RadioGroup v-model="formValidate.memberType">
          <Radio v-for="item in getterGroup(7)"  :key="item.dKey" :label="item.dKey">{{item.dValue}}</Radio>
        </RadioGroup>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="gender">
        <RadioGroup v-model="formValidate.gender + ''">
          <Radio label="1"></Radio>
          <Radio label="0"></Radio>
        </RadioGroup>
      </FormItem>
      </Col>
    </Row>
      <Row v-if="formValidate.memberType=='32'">
        <Col span="8">
        <FormItem label="" prop="companyId">
          <Select filterable v-model="formValidate.companyId" @on-change="selectCompany">
            <Option v-for="item in companyList" :value="item.id" :key="item.value">{{ item.companyName }}</Option>
          </Select>
        </FormItem>
        </Col>
        <Col span="8">
        <FormItem label="" prop="departmentId">
          <Select filterable v-model="formValidate.departmentId">
            <Option v-for="item in departmentList" :value="item.id" :key="item.value">{{ item.departmentName }}</Option>
          </Select>
        </FormItem>
        </Col>
      </Row>

    <Row>
      <Col span="8">
      <FormItem>
        <Checkbox style="margin-left:15px;margin-top:5px;" v-model="formValidate.isSecretary"></Checkbox>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="personnelType">
        <!-- <RadioGroup v-model="formValidate.personnelType">
            <Radio label="0"></Radio>
            <Radio label="1"></Radio>
            <Radio label="2"></Radio>
          </RadioGroup> -->
        <RadioGroup v-model="formValidate.personnelType">
          <Radio v-for="item in getterGroup(54)" :key="item.dKey" :label="item.dKey">{{item.dValue}}</Radio>
        </RadioGroup>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="memberClass">
        <Select filterable v-model="formValidate.memberClass">
              <Option v-for="item in getterGroup(41)" :value="item.dKey" :key="item.dKey">{{ item.dValue }}</Option>
            </Select>
      </FormItem>
      </Col>
    </Row>
    <Row>
      <Col span="8">
      <FormItem label="" prop="position">
        <Select filterable v-model="formValidate.position" placeholder="">
            <Option v-for="item in getterGroup(49)" :value="item.dKey" :key="item.dKey">{{ item.dValue }}</Option>
          </Select>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="jobTitle">
        <Select filterable v-model="formValidate.jobTitle" placeholder="">
            <Option v-for="item in getterGroup(50)" :value="item.dKey" :key="item.dKey">{{ item.dValue }}</Option>
          </Select>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="administrativeLevel">
        <Select filterable v-model="formValidate.administrativeLevel" placeholder="">
            <Option v-for="item in getterGroup(31)" :value="item.dKey" :key="item.dKey">{{ item.dValue }}</Option>
        </Select>
      </FormItem>
      </Col>
      </Row>
      <Row>
      <Col span="8">
      <FormItem label="" prop="passengerName">
        <Input :maxlength="28" v-model="formValidate.passengerName"></Input>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="country">
        <Select filterable v-model="formValidate.country" placeholder="">
              <Option v-for="item in getterCountry()" :value="item.countryCode" :key="item.countryCode">{{ item.cnName }}</Option>
          </Select>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="nation">
        <Select filterable v-model="formValidate.nation" placeholder="">
            <Option v-for="item in getterGroup(8)" :value="item.dKey" :key="item.dKey">{{ item.dValue }}</Option>
          </Select>
      </FormItem>
      </Col>
      </Row>
      <Row>
      <Col span="8">
      <FormItem label="" prop="cellPhone">
        <Input :maxlength="11" v-model="formValidate.cellPhone" placeholder=""></Input>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="backupCellPhone">
        <Input :maxlength="11" v-model="formValidate.backupCellPhone" placeholder=""></Input>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="phoneNum">
        <Input :maxlength="20" v-model="formValidate.phoneNum" placeholder=""></Input>
      </FormItem>
      </Col>
      </Row>
      <Row>
      <Col span="8">
      <FormItem label="" prop="email">
        <Input :maxlength="50" v-model="formValidate.email" placeholder=""></Input>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="paperNum">
        <Input :maxlength="20" v-model="formValidate.paperNum" placeholder=""></Input>
      </FormItem>
      </Col>
      <Col span="8">
      <FormItem label="" prop="birthday">
        <DatePicker   @on-change="(e)=>{
            changeDatePicker(e, 'birthday')
           }"  formate="yyyy-MM-dd" style="width:100%" type="date" placeholder="" ></DatePicker>
      </FormItem>
      </Col>
      </Row>
      <Row>
      <Col span="12">
      <FormItem label="" prop="remark">
        <Input type="textarea" :autosize="{minRows: 2,maxRows: 5}" v-model="formValidate.remark" placeholder=""></Input>
      </FormItem>
      </Col>
    </Row>
    <FormItem>
      <Button type="primary" @click="handleSubmit()">{{submitText}}</Button>
    </FormItem>
  </Form>
</template>

<script>
import {
  mapGetters
} from 'vuex'
import Axios from 'axios'
import Qs from 'qs'
export default {
  props: ['status'],
  data () {
    return {
      submitText: '',
      formValidate: {
        cardNum: '',
        realName: '',
        memberType: '1',
        gender: '1',
        isSecretary: false,
        companyId: '0',
        departmentId: '0',
        memberClass: '1',
        personnelType: '1',
        position: '03',
        jobTitle: '03',
        administrativeLevel: '1',
        passengerName: '',
        country: 'AF',
        nation: '33',
        cellPhone: '',
        backupCellPhone: '',
        phoneNum: '',
        email: '',
        paperNum: '',
        birthday: '',
        remark: ''
      },
      ruleValidate: {
        realName: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        birthday: [{
          required: true,
          type: 'string',
          message: '',
          trigger: 'change'
        }],
        paperNum: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
        {
          validator: function (rule, value, callback) {
            //
            var MobileRegex = /^[1-9]{1}[0-9]{14}$|^[1-9]{1}[0-9]{16}([0-9]|[xX])$/ // /\d{15}|\d{18}/
            if (!MobileRegex.test(value)) {
              callback(new Error(''))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }
        ],
        phoneNum: [{
          pattern: /^[0-9-()]{1,20}$/,
          message: '',
          trigger: 'blur'
        }],
        cellPhone: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
        {
          required: true,
          validator: function (rule, value, callback) {
            var MobileRegex = /^[1][0-9]{10}$/
            if (value == '' || value == null) {
              callback()
            }
            if (!MobileRegex.test(value)) {
              callback(new Error(''))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }
        ],
        backupCellPhone: [{
          validator: function (rule, value, callback) {
            var MobileRegex = /^[1][0-9]{10}$/
            if (value == '' || value == null) {
              callback()
            }
            if (!MobileRegex.test(value)) {
              callback(new Error(''))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }],
        email: [{
          required: true,
          message: '',
          trigger: 'blur'
        },
        {
          required: true,
          validator: function (rule, value, callback) {
            var MobileRegex = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
            if (value == '' || value == null) {
              callback()
            }
            if (!MobileRegex.test(value)) {
              callback(new Error(''))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }
        ]
      },
      departmentList: [],
      adminList: [],
      shippingList: [],
      countryList: [],
      nationList: [],
      companyList: [{
        value: '0',
        label: ''
      },
      {
        value: '1',
        label: ''
      }
      ]
    }
  },
  computed: {
    ...mapGetters([
      'getterGroup',
      'getterCountry'
    ])
  },
  methods: {
    selectCompany () {
      this.setDipartmentList(this.formValidate.companyId)
    },
    cancelModal () {
      let cur_modal = this.$store.state.modal
      cur_modal.curtime = new Date().getTime()
      cur_modal.status = false
      this.$store.commit('modalStatus', cur_modal)
    },
    changeDatePicker (e, dateVal) {
      this.formValidate[dateVal] = e
    },
    refreshTable () {
      this.$store.commit('refreshTable', { curtime: new Date().getTime() })
    },
    setDipartmentList (companyId) {
      Axios({
        method: 'get',
        url: `/api/companyDepartment/list?companyId=${companyId}`,
        headers: {
          userId: this.$store.state.user.userId
        }
      }).then(res => {
        this.departmentList = res.data.data.list
      })
    },
    submiteData (submiteData) {
      let sumitUrl = `/api/member/add`
      if (this.status == 'update') {
        sumitUrl = '/api/member/update'
      }
      let data = Qs.stringify(submiteData)
      Axios({
        method: 'post',
        url: sumitUrl,
        data: data,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
          'userId': this.$store.state.user.userId
        }
      }).then(res => {
        if (!this.status && res.data.code === 200) {
          this.cancelModal()
          this.refreshTable()
          this.$refs.formValidate.resetFields()
        } else if (this.status && res.data.code === 200) {
          this.$Message.success('')
        } else {
          console.log('error')
        }
      })
    },
    async handleSubmit () {
      this.$refs.formValidate.validate((valid) => {
        if (valid) {
          let submiteData = this.formValidate
          if (submiteData.memberType == '1') {
            submiteData.companyId = 0
            submiteData.departmentId = 0
          }
          if (submiteData.isSecretary) {
            submiteData.isSecretary = 1
          } else {
            submiteData.isSecretary = 0
          }
          submiteData.cardNum = this.$route.query.id
          // this.$refs.formValidate.resetFields()
          this.submiteData(submiteData)
        }
      })
    },
    getMemberDetail () {
      let memberId = this.$route.query.id
      Axios({
        method: 'get',
        url: `/api/member/${memberId}`,
        headers: {
          userId: this.$store.state.user.userId
        }
      }).then(res => {
        this.formValidate = res.data.data
      }).catch(function (error) {
        // console.log(error)
      })
    }
  },
  mounted () {
    if (this.status == 'update') {
      this.getMemberDetail()
      this.submitText = ''
    }
    //  
    Axios({
      method: 'get',
      url: '/api/company/list?&size=1000',
      headers: {
        userId: this.$store.state.user.userId
      }
    }).then(res => {
      this.companyList = res.data.data.list
      if (res.data.data.list.length > 0) {
        let companyId = res.data.data.list[0].id
        this.formValidate.companyId = companyId
        this.setDipartmentList(companyId)
      }
      //  /companyDepartment/list
    }).catch(function (error) {
      console.log(error)
    })
  }
}
</script>

